import { Component, OnInit, ViewChild } from '@angular/core';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { ModalImportCsvComponent } from 'src/app/components/modal-import-csv/modal-import-csv.component';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { CategoryManagementService } from './category-management.service';
import { ModalEditCategoryComponent } from 'src/app/components/modal-edit-category/modal-edit-category.component';

@Component({
  selector: 'app-category-management',
  templateUrl: './category-management.page.html',
  styleUrls: ['./category-management.page.scss'],
})
export class CategoryManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  lstCategory: any = [];
  listFilter: any = [];
  selectedFilter: "All";
  heightTable = "775";  
  constructor(private popoverController: PopoverController,
    public categoryService: CategoryManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) {        
  
  }

  ngOnInit() {    
    this.loadData();   
    this.utilsService.observerCategoryLoaded.subscribe(() => {
      this.loadData();   
    });  
  }  

  ionViewDidEnter() {
    this.heightTable = this.contentSearch.el.offsetHeight;
  }
  
  loadData(){
    this.utilsService.loadingStart();
      let tableDataTemp = [];
      this.lstCategory = [];
      this.listFilter = [];
      this.listFilter.push({"id": "All", "value": "All"});
      this.selectedFilter = "All";
      for (let index = 0; index < this.utilsService.lstCategory.length; index++) {
        const element = this.utilsService.lstCategory[index];
        if(element.value.split("-").length == 1){
          this.listFilter.push({"id": element.value, "value": element.title});
        }
        tableDataTemp.push(element);
      }  
      this.lstCategory = tableDataTemp;
      this.utilsService.loadingStop();    
  }
  filterContent(ev){
    this.lstCategory = [];
    this.selectedFilter = ev.target.value;
    if(this.selectedFilter != "All"){

      this.lstCategory = this.utilsService.lstCategory.filter((item) => {
        return item.value.split("-")[0] == ev.target.value;      
      });
    }
    else{
      for (let index = 0; index < this.utilsService.lstCategory.length; index++) {
        const element = this.utilsService.lstCategory[index];
        this.lstCategory.push(element);
      }  
    }    
  }
  showDetail(data: any) {
    if(data.ev.toElement.outerHTML.includes('tabulator-field="title"')){
      this.showModalEditCategory(data.ev, data.row._row.data, null);
    } 
    else if(data.ev.toElement.outerHTML.includes('<i class="fa fa-plus"></i>')){      
      this.showModalEditCategory(data.ev, null, data.row._row.data);
    }  
  }

  async showModalEditCategory(ev: any, row: any, parent: any) {
    const popover = await this.popoverController.create({
      component: ModalEditCategoryComponent,
      id: "editCategoryModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row, parentValue: parent },
      cssClass: "edit-category-popover"
    });
    return await popover.present();
  }
  async showModalImport(ev: any) {
    const popover = await this.popoverController.create({
      component: ModalImportCsvComponent,
      event: ev,
      translucent: true,
      componentProps: { mode: "category", sheetName: "Category" },
      cssClass: "import-csv-popover"
    });
    popover.onDidDismiss()
      .then((result) => {
        if (result.data && result.data != "close") {
          this.utilsService.loadingStart();
          this.categoryService.importToDB(result.data).subscribe((result) => {
            this.utilsService.loadingStop();
            if (result == true) {
              this.utilsService.showInfoSystem("Import thành công!");
            }
            else {
              this.utilsService.showError("Import thất bại!");
            }
          },
            error => {
              this.utilsService.showError(error);
            });
        }
        else {
          return true;
        }
      },
        error => {
          this.utilsService.showError(error);
        }
      );
    return await popover.present();
  }
}
